import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, ModalController } from 'ionic-angular';
import { ScreenOrientation } from '@ionic-native/screen-orientation';
import { AidePage } from '../aide/aide';
/**
 * Generated class for the HistoriquePage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-historique',
  templateUrl: 'historique.html',
})
export class HistoriquePage {
  public question: string = "";
  constructor(public navCtrl: NavController, public navParams: NavParams, public sc: ScreenOrientation, public modalCtrl: ModalController) {
    this.sc.lock(this.sc.ORIENTATIONS.PORTRAIT);
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad HistoriquePage');
  }

  aide() {
    this.navCtrl.push(AidePage);
  }

  openModalHisto() {

    let modal = this.modalCtrl.create('ModalhistoPage');
    modal.present();
  }
}



